/* eslint-disable no-nested-ternary */
import React, { useMemo } from 'react';
import { useTable, useFilters, useSortBy } from 'react-table';
import { useSelector } from 'react-redux';
import './table.scss';
import ColumnFilter from '../columnFilter/ColumnFilter';

export default function Table() {
  const citiesTable = useSelector((state) => state.searchSliceReducer.citiesTable);

  const data = useMemo(
    () => citiesTable.map((item, num) => ({
      col1: num + 1,
      col2: item.name,
      col3: item.full_name,
      col4: item.telcod,
    })),

    [citiesTable],
  );

  const columns = useMemo(() => [
    {
      Header: ' /',
      accessor: 'col1',
    },
    {
      Header: ' ',
      accessor: 'col2',
      Filter: ColumnFilter,
    },
    {
      Header: '  ',
      accessor: 'col3',
      Filter: ColumnFilter,
    },
    {
      Header: '  ',
      accessor: 'col4',
      Filter: ColumnFilter,
    },
  ], []);

  const tableSities = useTable({ columns, data }, useFilters, useSortBy);
  console.log(tableSities);

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = tableSities;
  // eslint-disable-next-line max-len
  // console.log('getTableProps', getTableProps()); // {role: 'table'}

  // eslint-disable-next-line max-len
  // console.log('headerGroups.getHeaderGroupProps', headerGroups[0].getHeaderGroupProps()); // {key: 'headerGroup_0', role: 'row'}
  // eslint-disable-next-line max-len
  // console.log('headerGroups', headerGroups); // [0: {headers: Array(4), getHeaderGroupProps: , getFooterGroupProps: }]

  return (
    <table {...getTableProps()}>
      <thead>
        {headerGroups.map((headerGroup) => (
          <tr {...headerGroup.getHeaderGroupProps()}>
            {
            headerGroup.headers.map((column) => (
              <th className="th" {...column.getHeaderProps(column.getSortByToggleProps())}>
                {column.render('Header')}
                {/* <div>{column.canFilter ? column.render('Filter') : null}</div> */}
                <span>{
                column.isSorted
                  ? column.isSortedDesc
                    ? ' '
                    : ' '
                  : ''
            }
                </span>
              </th>
            ))
}
          </tr>
        ))}
      </thead>
      <tbody {...getTableBodyProps()}>
        {
          rows.map((row) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => (
                  <td className="tb" {...cell.getCellProps()}>
                    {
                    cell.render('Cell')
}
                  </td>
                ))}
              </tr>
            );
          })

        }

      </tbody>
    </table>
  );
}

